.user {
  position: relative;
  height: 100%;
  overflow: auto;
  background: url(./img/bg.svg) no-repeat center 110px/100%;

  &__content {
    width: 368px;
    margin: 0 auto;
  }

  &__title {
    margin-bottom: 20px;
    font-size: 16px;
  }

  &__lang {
    position: absolute;
    top: 24px;
    right: 24px;
    font-size: 16px;
    cursor: pointer;
  }
}

.header {
  padding: 72px 0 40px;
  overflow: hidden;
  text-align: center;

  &__title {
    height: 44px;
    font-family: Avenir, helvetica neue, Arial, Helvetica, sans-serif;
    font-size: 33px;
    font-weight: 600;
    line-height: 44px;
    color: rgba(0, 0, 0, 0.85);
  }

  &__logo {
    width: auto;
    height: 100%;
    margin-right: 16px;
    vertical-align: top;
  }

  &__subtitle {
    margin-top: 12px;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.45);
    text-align: center;
  }
}

.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 24px 16px;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.45);
  text-align: center;

  &__copyright {
    color: rgba(0, 0, 0, 0.45);
  }

  &__links {
    margin-bottom: 12px;
  }

  &__link {
    color: rgba(0, 0, 0, 0.45);

    &:hover {
      color: rgba(0, 0, 0, 0.65);
    }

    & + & {
      margin-left: 40px;
    }
  }
}

.login {
  &__remember {
    margin-bottom: 24px;
  }

  &__link-register {
    text-align: center;
  }

  &__captcha {
    display: block;
    font-size: 0;
    text-align: right;

    svg {
      height: 40px;
      background: linear-gradient(180deg, #d9ecff 0, #d9dadc);
    }
  }
}

.register {
  &__captcha-button {
    width: 100% !important;
    padding-right: 0;
    padding-left: 0;
  }

  &__popover-wrapper {
    position: relative;
  }

  &__popover-placeholder {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 40px;
    visibility: hidden;
    opacity: 0;
  }

  &__popover-content {
    width: 200px;
    padding: 4px 0;
  }

  &__progress-color {
    transition: color 0.3s;

    &.success {
      color: #52c41a;
    }

    &.exception {
      color: #ff4d4f;
    }

    &.normal {
      color: #1890ff;
    }
  }

  &__password-tips {
    margin-top: 10px;
  }
}

@media screen and (max-width: 576px) {
  .user {
    &__content {
      width: 95%;
    }
  }

  .header {
    &__title {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
